import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Star } from "lucide-react"

const products = [
  {
    id: 1,
    name: "Wool Blend Coat",
    price: "$129.99",
    rating: 5,
    image: "/placeholder.svg?height=400&width=300",
    category: "Outerwear",
  },
  {
    id: 2,
    name: "Cashmere Sweater",
    price: "$89.99",
    rating: 4,
    image: "/placeholder.svg?height=400&width=300",
    category: "Knitwear",
  },
  {
    id: 3,
    name: "Leather Ankle Boots",
    price: "$149.99",
    rating: 5,
    image: "/placeholder.svg?height=400&width=300",
    category: "Footwear",
  },
  {
    id: 4,
    name: "Silk Scarf",
    price: "$59.99",
    rating: 4,
    image: "/placeholder.svg?height=400&width=300",
    category: "Accessories",
  },
]

export default function BestSelling() {
  return (
    <div>
      <h2 className="text-4xl font-serif text-center mb-6">Best Selling Products</h2>
      <p className="text-center text-muted-foreground max-w-2xl mx-auto mb-12">
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe voluptas ut dolorum consequuntur, adipisci
        repellat! Eveniet commodi voluptatem.
      </p>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
        {products.map((product) => (
          <div key={product.id} className="group">
            <div className="relative overflow-hidden rounded-lg mb-4">
              <div className="relative h-[350px] w-full overflow-hidden bg-muted/20">
                <Image
                  src={product.image || "/placeholder.svg"}
                  alt={product.name}
                  fill
                  className="object-cover transition-all duration-500 group-hover:scale-110"
                />
              </div>
              <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-opacity flex items-center justify-center">
                <Button className="bg-white text-black hover:bg-gray-200">Quick View</Button>
              </div>
              <div className="absolute top-4 left-4 bg-white px-3 py-1 text-xs rounded">{product.category}</div>
            </div>
            <div>
              <div className="flex mb-2">
                {Array(5)
                  .fill(0)
                  .map((_, i) => (
                    <Star
                      key={i}
                      className={`h-4 w-4 ${i < product.rating ? "fill-yellow-400 text-yellow-400" : "text-gray-300"}`}
                    />
                  ))}
              </div>
              <h3 className="font-medium">{product.name}</h3>
              <p className="text-muted-foreground">{product.price}</p>
              <Button className="mt-4 w-full bg-black hover:bg-gray-800 text-white">Add Cart</Button>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}

